@import url('https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap');

:root{
    --primary-color: #4458dc;
    --primary-color2: #854fee;
    --title-color: #141313;
    --text-color: #777777;
    --border-color: #201f1f17;
    --gradient-color: linear-gradient(90deg, #f12711 0%, #f5af19 100%);
    --shadow-color: 0 10px 30px rgba(118, 85, 225, 0.3);
    --box-shadow: 0 10px 30px rgba(57, 56, 61, 0.205);
    --yusei: 'Yusei Magic', sans-serif;
}