@media screen and (max-width : 450px) {
    .button{
        padding: .8rem 0.77rem;
        font: normal 500 12px/20px var(--yusei);
    }
    p.p-text{
        font-size: 12px;
    }
    .navigation-panel .navbar,
    .main-content .service-panel
    {
        padding: 0 !important;
        margin: 0;
    }
    .main-content .service-panel .service-wrap{
        padding: 20px;
    }
    .navigation-panel .navbar a img{
        width: 150px;
    }
    .main-content .banner-panel .banner-title h3{
        padding-top: 10%;
    }
    .title-text, .main-content .banner-panel .banner-title h1{
        font: normal bold 25px/2rem var(--yusei);
    }
    .main-content .banner-panel .banner-title h4{
        font: normal bold 18px/2rem var(--yusei);
    }
    .main-content .banner-panel .banner-image > img {
        padding: 2rem 0;
    }
    .main-content .service-panel .service-wrap h1{
        font-size: 24px;
    }
    .get-touch{
        text-align: center;
    }
    footer.footer-content{
        padding: 20px;
    }
    footer.footer-content .social .social-icons {
        width: 200px;
        margin: auto;
    }
}