@import "_variable.css";

body{
    font-family: var(--yusei);
}
h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1{
    line-height: 3;
}

/* global styles */
.nav-slider{
    position: relative;
}
.nav-slider::before{
    content: "";
    position: absolute;
    background: var(--gradient-color);
    z-index: -1;
    transform: skew(-10deg);

    top: -0.5em;
    left: -0.5em;
    width: 0;
    height: calc(100% + 1em);

    transition: width 250ms;
}
.nav-slider:hover::before{
    color: white;
    width: calc(100% + 1em);
    transition: width 250ms;
}
button:focus,
button:active
button:hover
.btn:active,
.btn.active{
    outline: 0;
    box-shadow: none;
}
.title-h1{
    font: normal bold 39px/40px var(--yusei);
}
.title-text{
    font: normal bold 50px/6rem var(--yusei);
}
.button{
    padding: .8rem 2.2rem;
    font: normal 500 16px/20px var(--yusei);
    position: relative;
    border: 3px solid transparent;
    border-radius: 4px;
}
.button.primary-button{
    background: var(--gradient-color);
    background-clip: padding-box;
    color: whitesmoke;
    transition: background .3s ease;
    box-shadow: var(--box-shadow);
}
.button.primary-button:hover{
    background: whitesmoke;
    background-clip: padding-box;
    color: black;
}
.button.primary-button::after{
    position: absolute;
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    background: var(--gradient-color);
    content: '';
    z-index: -1;
    border-radius: 4px;
}
.navbar-fixed{
    position: fixed;
    width: 100%;
    top: -1px;
    left: 0;
    right: 0;
    backdrop-filter: blur(15px);
    box-shadow: var(--box-shadow);
    z-index: 999;
    transition: background .7s ease;
}
/* end global styles */

/* navigation */
.navigation-panel .navbar .navbar-brand{
    padding: 0 2rem 0 5rem;
}
.navigation-panel .navbar{
    padding: 1rem 5rem;
}
.navigation-panel .navbar .nav-item .nav-link{
    font: normal 500 15px/2px var(--yusei);
    text-transform: uppercase;
    padding: 1.7rem;
    color: var(--title-color);
}
.navigation-panel .navbar .navbar-nav a:hover{
    color: white;
}
/* end navigation */

/* hero */
.main-content .banner-panel .banner-title .banner-wrap{
    background: var(--gradient-color);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}
.main-content .banner-panel .banner-title h3{
    padding-top: 35%;
    font-weight: 500;
}
.main-content .banner-panel .banner-title h3::after{
    content: "";
    height: 2px;
    width: 18vw;
    background: var(--gradient-color);
    display: inline-block;
    margin-left: 5%;
    transform: translateY(-10px);
}
.main-content .banner-panel .banner-title h1{
    font-size: 4rem;
}
.main-content .banner-panel .banner-title h4{
    font-size: 1.5rem;
}
.main-content .banner-panel .banner-image > img{
    padding: 7rem 0;
    width: 90rem;
}
/* end hero */

/* services */
.main-content .service-panel{
    padding: 7rem 2rem;
}
.main-content .service-panel .service-wrap{
    padding: 0 14rem;
}
.main-content .service-panel .service-wrap h1{
    font-size: 35px;
}
.main-content > .service-panel .service{
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
    cursor: pointer;
    transition: box-shadow.7s ease;
}
.main-content .service-panel .service .service-img img, .main-content .service-panel .service .service-img video{
    width: 100%;
    height: 350px;
    -o-object-fit: cover;
    object-fit: cover;
}
.main-content .service-panel .service:hover{
    box-shadow: var(--box-shadow);
}
/* end services */

/* footer */
footer.footer-content{
    padding: 2rem 0;
}
footer.footer-content h3{
    font: normal 600 28px/60px var(--yusei);
}
footer.footer-content p.paragraph{
    font: normal 14px/20px var(--yusei);
    text-align: justify;
}
footer.footer-content .social .social-icons{
    display: flex;
    justify-content: space-around;
    width: 250px;
    margin: 2rem -20px;
}
footer.footer-content ul{
    list-style: none;
    padding-left: 0;
}
footer.footer-content ul li.menu-item a.menu-link{
    font: normal 500 18px/35px var(--yusei);
    text-decoration: none;
    color: #5a5959;
    cursor: pointer;
}
footer.footer-content ul li.menu-item a.menu-link:hover{
    color: #141313;
}
footer.footer-content .social a > svg{
    font-size: 35px;
    color: var(--border-color);
}
footer.footer-content .social a:hover > svg{
    color: var(--primary-color);
}
/* end footer */